<template lang="html">
  <div id="jobs-component">
      <apexchart type="area" height="180" :options="a.chartOptions" :series="a.series" v-for="(a, index) in areaData" v-bind:key="index"></apexchart>
  </div>
</template>

<script>
import VueApexCharts from 'vue-apexcharts'

export default {
    name: 'Jobs',
    props: [ 'idIndex' ],
    data: function() {
        return {
            areaData: this.getAreaData()
            // series: [{
            //   name: '0_3',
            //   data: [0.2549, 0.0857, 0]
            // }],
            // chartOptions: {
            //   chart: {
            //     height: 180,
            //     type: 'area',
            //     zoom: {
            //         enabled: false
            //     }
            //   },
            //   title: {
            //     text: "Job name: 0_4",
            //     align: 'center',
            //     margin: 10,
            //     offsetX: 0,
            //     offsetY: 0,
            //     floating: false,
            //     style: {
            //       fontSize:  '16px',
            //       color:  '#263238'
            //     }
            //   },
            //   dataLabels: {
            //     enabled: true
            //   },
            //   stroke: {
            //     curve: 'straight'
            //   },
            //   xaxis: {
            //     type: 'category',
            //     categories: ["0.2499", "0.4191", "0.5049"]
            //   },
            //   tooltip: {
            //       enabled: false
            //   }
            //
            // },
        }
    },
    mounted: function() {

    },
    methods: {
        getAreaData: function() {
            var res = []
            if(this.idIndex == -1) return res
            var data = this.GLOBAL.schedCore.running.serverLog[parseInt(this.idIndex)].list
            for(var i = 0; i < data.length; i++) {
                var seriesData = []
                var categories = []
                var job = this.GLOBAL.schedCore.running.jobsDict[data[i].name]
                for(var j = 0; j < job.log.length; j++) {
                    seriesData.push(job.log[j].size.toFixed(4))
                    categories.push(job.log[j].time.toFixed(4))
                }
                res.push({
                    'series': [{ name: '0_3', data: seriesData }],
                    'chartOptions': { chart: { height: 180, type: 'area', zoom: { enabled: false } }, title: { text: "Job name: " + data[i].name, align: 'center', margin: 10, offsetX: 0, offsetY: 0, floating: false, style: { fontSize:  '16px', color:  '#263238' } }, dataLabels: { enabled: true }, stroke: { curve: 'straight' }, xaxis: { type: 'category', categories: categories }, tooltip: { enabled: false } },
                })
            }

            return res
        }
    },
    components: {
        apexchart: VueApexCharts,
    },
}
</script>

