    methods: {
        resizeJobCube: function () {
            if(document.body.contains(document.getElementById('overview-content'))) {
                document.getElementById('overview-content').style.height = (document.body.offsetHeight - 270) + "px"
                // document.getElementById('ocb-left-right-middle').style.lineHeight = document.getElementById('ocb-left-right-middle').offsetHeight + 'px'

                var jobClass = document.getElementsByClassName('ocb-job-generator')
                var lineHeight = document.getElementById('ocb-left-left').offsetHeight / this.jobGenerator.length
                for(var i = 0; i < jobClass.length; i++) {
                    jobClass[i].style.lineHeight = lineHeight + 'px'
                }
            }

