        convertFunction: function(sizeFun) {
            var res = 0
            if(sizeFun == 'exponential') {
                res = 'E()'
            }else if(sizeFun == 'deterministic') {
                res = 'D()'
            }else if(sizeFun == 'uniform') {
                res = 'U(,) '
            }else if(sizeFun == 'erlang') {
                res = 'E(,)'
            }else if(sizeFun == 'pareto') {
                res = 'P(,,)'
            }
            return res
        },
        convertSpeedUpFunction: function(name, para) {
            if(name == 'power') {
                return '()=^ =' + para
            }else if(name == 'amdahl') {
                return '()=1/(1+/) =' + para
            }else  {
                return '('+para+')'
            }
        },
