            g.style.position = "absolute"
            g.style.left = data1.x + 'px'
            g.style.top = data1.y + 'px'
            document.body.appendChild(g)

            var tl = gsap.timeline();
            var id = '#realjob'
            tl.to(id, {duration: 1, x: data2.x, y: data2.y})
            // tl.to(id, {duration: 1, opacity:0, x: 500, y: 100, scale: 0.5})

