<template lang="html">
    <div id="simulation">
        <div id="nav">
            <div @click="goToHome()" id="logo">
                <span class="logo-img">
                    <img src="../assets/logo.png" alt="simulation system">
                </span>
                <span class="logo-title">Simulation System</span>
            </div>
            <div id="icon-button">
                <span @click="saveFile()"><i class="left-iconfont menu-icon" style="color:#0780df!important;">&#xe629;</i></span>
                <span @click="callShedulingAlgorithmTimer()" v-show="!this.status"><i class="left-iconfont menu-icon"  style="color:#36bea6!important;"> &#xe6df;</i></span>
                <span @click="cancelTimeHandler()" v-show="this.status"><i class="left-iconfont menu-icon" style="color:#f62d51!important;">&#xe65e;</i></span>
                <span class=" menu-icon">|</span>
                <span class="menu-icon"  style="color:#f62d51!important;" v-show="!this.status">stoping</span>
                <span class="menu-icon" style="color:#36bea6!important;" v-show="this.status">runing</span>
            </div>
        </div>
        <div id="wrap">
            <div id="left">
                <div style="width:100%;height:60px;"></div>
                <div id="left-top"></div>
                <div id="left-down">
                    <span class="left-icon-name" @click="getViewByIndex(0)" v-bind:class="{'options-focus': menuOptions==0}">
                        <i class='left-iconfont'>&#xe679;</i>
                        <span class="left-name" >Overview</span>
                    </span>
                    <span class="left-icon-name" @click="getViewByIndex(1)" v-bind:class="{'options-focus': menuOptions==1}">
                        <i class='left-iconfont'>&#xe610;</i>
                        <span class="left-name">Mean Time</span>
                    </span>
                    <span class="left-icon-name" @click="getViewByIndex(2)" v-bind:class="{'options-focus': menuOptions==2}">
                        <i class='left-iconfont'>&#xe635;</i>
                        <span class="left-name">Utilization</span>
                    </span>
                    <!-- <span class="left-icon-name" @click="getViewByIndex(3)" v-bind:class="{'options-focus': menuOptions==3}">
                        <i class='left-iconfont'>&#xe679;</i>
                        <span class="left-name">Jobs</span>
                    </span> -->
                </div>

                <div id="left-statistic">
                    <div id="pie-chart">
                    </div>
                </div>

            </div>
            <div id="right">
                <div id="right-title">
                    {{menuName}}
                </div>
                <div id="right-view">
                    <right-component :ref="currentView" v-bind:is="currentView"></right-component>
                </div>
            </div>

        </div>
    </div>
</template>

<script>
import Overview from '../components/Overview.vue'
import MeanTime from '../components/MeanTime.vue'
import Utilizations from '../components/Utilizations.vue'
import Jobs from '../components/Jobs.vue'
// import Algorithm from '../components/Algorithm.vue'
import FileSaver from 'file-saver'
import Swal from 'sweetalert2'

var echarts = require('echarts');


export default {
    name: 'Simulation',
    data: function() {
        return {
            // style
            menuOptions: 0,
            menuName: 'Overview',
            currentView: 'Overview',
            config: this.GLOBAL.schedCore.config,
            // current
            status: this.GLOBAL.schedCore.status,

            timerHandler: null,

            load: this.GLOBAL.schedCore.config.load,

            // chart

        }
    },
    components: {
        Overview,
        MeanTime,
        Utilizations,
        Jobs
    },
    mounted: function() {
        window.onbeforeunload = function(event){
            return true
        }
        // style
        const config = this.GLOBAL.schedCore.config
        if(config.jobs.length == 0 ||
            config.arrivalRate == null ||
            config.serverCluster == null
            // algorithmName == null ||
            // algorithm == null
        ) {
            this.$router.push({name: "Index"})
        }
        this.initialize()
        this.load = this.GLOBAL.schedCore.config.load
        this.generatePie()
    },

    methods: {
        goToHome: function() {
            var that = this
            if(that.GLOBAL.schedCore.status) {
                alert("Please stop the simulation")
                return false
            }
            Swal.fire({
              title: 'Are you sure to leave this page?',
              text: "The data will be cleaned",
              icon: 'warning',
              showCancelButton: true,
              confirmButtonColor: '#3085d6',
              cancelButtonColor: '#d33',
              confirmButtonText: 'Yes, leave now!'
            }).then((result) => {
              if (result.value) {
                that.GLOBAL.schedCore = that.GLOBAL.schedCoreBackUp
                that.$router.push({ name: "Index"})
              }
            })

        },
        getViewByIndex: function(index) {
            if(index == 0) {
                this.menuName = 'Overview'
                this.currentView = 'Overview'
            }else if(index == 1) {
                this.menuName = 'Mean Time'
                this.currentView = 'MeanTime'
            }else if(index == 2) {
                this.menuName = 'Utilization'
                this.currentView = 'Utilizations'
            }else if(index == 3) {
                this.menuName = 'Algorithm'
                this.currentView = 'Algorithm'
            }
            this.menuOptions = index
        },
        saveFile: function() {
            if(this.GLOBAL.schedCore.status) {
                alert("You should stop first")
                return
            }
            var blob = new Blob([JSON.stringify(this.GLOBAL.schedCore)], {type: "text/plain;charset=utf-8"});
            FileSaver.saveAs(blob, "simulation.json");

        },
        control: function() {
            this.status = !this.status
            this.GLOBAL.schedCore.status = this.status
        },
        getPieData: function(val) {
            var res = []
            for(var i = 0; i < this.GLOBAL.schedCore.config.jobs.length; i++) {
                if(val == 'colors') {
                    res.push(this.GLOBAL.schedCore.config.jobs[i].color)
                }else {
                    res.push({
                        value: this.GLOBAL.schedCore.config.jobs[i].proportionVal,
                        name: 'Job generator ' + this.GLOBAL.schedCore.config.jobs[i].id
                    })
                }
            }
            return res
        },
        generatePie: function () {
            var myChart = echarts.init(document.getElementById('pie-chart'))
            this.GLOBAL.schedCore.config.jobs.sort(function(a, b){
                return parseInt(a.id) - parseInt(b.id)
            })
            var that = this
            myChart.setOption({
                title: {
                    text: 'Job Generators',
                    subtext: 'rate  = '+that.GLOBAL.schedCore.config.arrivalRate.toString()+'    load  = ' + that.load.toFixed(2).toString(),
                    left: 'center',
                    top: 0,
                    textStyle: {
                        color: '#ffbc34',
                        fontSize: 16
                    }
                },
                tooltip: {
                    trigger: 'item',
                    // formatter: '{a} <br/>{b}: {c} ({d}%)'
                    formatter: '{d}%'
                },
                color: that.getPieData('colors'),
                series: [
                    {
                        name: 'Details',
                        type: 'pie',
                        radius: ['50%', '70%'],
                        avoidLabelOverlap: false,
                        label: {
                            normal: {
                                show: false,
                                position: 'center'
                            },
                            emphasis: {
                                show: true,
                                textStyle: {
                                    fontSize: '12',
                                    fontWeight: 'bold'
                                }
                            }
                        },
                        labelLine: {
                            normal: {
                                show: false
                            }
                        },
                        data: that.getPieData('data')
                    }
                ]
            })
            this.GLOBAL.generatorPieOption = myChart
            myChart.on('mouseover', function (params) {
                var index = params.dataIndex
                var id = parseInt(params.name.split(' ')[2])
                if(that.currentView == 'Overview') {
                    that.$refs.Overview.$refs.popperRef[id].showPopper = true
                }

            })
            myChart.on('mouseout', function (params) {
                var index = params.dataIndex
                var id = parseInt(params.name.split(' ')[2])
                if(that.currentView == 'Overview') {
                    that.$refs.Overview.$refs.popperRef[id].showPopper = false
                }
            })
            // myChart.dispatchAction({
            //     type: 'showTip',
            //     // series ID, can incoming in option
            //     seriesIndex: 0,
            //     // data name
            //     dataIndex: 1
            //     // table of all legend selecting states
            //     // selected: Object
            // })
            // myChart.dispatchAction({
            //     type: 'highlight',
            //     // series ID, can incoming in option
            //     seriesIndex: 0,
            //     // data name
            //     dataIndex: 1
            //
            // })
        },
        initialize: function() {
            if(this.GLOBAL.schedCore.running.jobGeneratorQueue.length == 0) {
                var sum = 0
                for(var i = 0; i < this.GLOBAL.schedCore.config.jobs.length; i++) {
                    var obj = this.GLOBAL.schedCore.config.jobs[i]
                    this.GLOBAL.schedCore.running.jobGeneratorQueue.push(
                        new this.GLOBAL.Objects.JobGenerator(
                            obj
                        )
                    )
                    sum += this.GLOBAL.schedCore.running.jobGeneratorQueue[i].mean
                }
                this.GLOBAL.schedCore.config.load = this.GLOBAL.schedCore.config.arrivalRate * sum / this.GLOBAL.schedCore.config.jobs.length
                // console.log(this.GLOBAL.schedCore.config.load)
                this.GLOBAL.schedCore.running.availableNodeNum = this.GLOBAL.schedCore.config.serverCluster
            }else {
                // var newJobGeneratorQueue = []

            }



            // console.log('Initialized')
        },
        callShedulingAlgorithmTimer: function() {
            this.control()
            this.timerHandler = setInterval(() => {
                this.callShedulingAlgorithm()
            }, 200)
        },
        cancelTimeHandler: function() {
            this.control()
            clearInterval(this.timerHandler)
        },
        callShedulingAlgorithm: function() {
            // this.initialize()]
            // console.log(this.GLOBAL.schedCore)
            var schedCore = this.GLOBAL.schedCore
            var functionns = {
                getMeanTime: this.GLOBAL.getMeanTime,
                getNewJobs: this.GLOBAL.getNewJobs,
                speedUpFunc: this.GLOBAL.speedUpFunc
            }

            // this.GLOBAL.schedCore = this.GLOBAL.shedulingAlgorithm(schedCore, functionns)
            try {
                var res = eval(this.GLOBAL.schedCore.config.algorithm + " shedulingAlgorithm(schedCore, functionns)")
                this.GLOBAL.schedCore = res
            } catch (e) {
                this.cancelTimeHandler()
                alert('Your function has a syntax error, please check the details')
                alert(e)
            }


            // this.GLOBAL.schedCore.running.queue = res.queue
            // this.GLOBAL.schedCore.running.serverTaskList = res.serverInfo.serverTaskList
            // this.GLOBAL.schedCore.running.availableNodeNum = res.serverInfo.availableNodeNum
            // this.GLOBAL.schedCore.running.jobsDict = res.jobsDict
            // this.GLOBAL.schedCore.running.completedQueue = res.completedQueue
            // this.GLOBAL.schedCore.running.jobGeneratorQueue = res.jobGeneratorQueue
            // this.GLOBAL.schedCore.running.serverLog = res.serverLog
            // this.GLOBAL.schedCore.globalTime = res.currentTime
            // this.GLOBAL.schedCore.running.availableNodeNum =

            // console.log(res)
            // if(this.GLOBAL.schedCore.running.completedQueue.length != 0) {
            //     console.log(this.GLOBAL.schedCore.running.totalCostTimeForCompletedJobs / this.GLOBAL.schedCore.running.completedQueue.length)
            // }
            // console.log(this.GLOBAL.schedCore.running.totalJobsNum)
            // console.log(this.GLOBAL.schedCore.globalTime)
            // console.log('-------')
            this.$refs[this.currentView].changeGlobalData("");
        }

    }

}
</script>

