<template lang="html">
    <div class="utilizations">
        <div id='main'>

        </div>
        <div id="jobs-details" v-show="false">
            <jobs v-bind:idIndex="id"></jobs>
            <!-- <apexchart type="area" height="350" :options="chartOptions" :series="series"></apexchart> -->
        </div>
    </div>
</template>

<script>
import Swal from 'sweetalert2'
import Jobs from './Jobs.vue'
import Vue from 'vue'

var echarts = require('echarts');
export default {
    name: 'Utilizations',
    data: function() {
        return {
            id: -1,
            time: [],
            utilizationsList: [],
            // meanTime: [2,3,4,5,6,6,1],
            // numList: [2,3,4,5,6,6,1]
        }
    },
    mounted: function() {
        this.getList()
        this.generateGraph()
    },
    methods: {
        getList: function() {

            var len = this.GLOBAL.schedCore.running.serverLog.length
            for(var m = 0; m < this.GLOBAL.schedCore.config.serverCluster; m++) {
                var utilObj = {
                    "name": 1,
                    "type": "bar",
                    "stack": "sta1",
                    "label": {
                        "normal": {
                            "show": true,
                            "position": "inside"
                        }
                    },
                    "color": '#'+(Math.random()*0xFFFFFF<<0).toString(16),
                    "data": []
                }
                this.utilizationsList.push(
                    utilObj
                )
            }
            for (var i = 0; i < len; i++) {
                var obj = this.GLOBAL.schedCore.running.serverLog[i]
                this.time.push(obj.time.toFixed(5))
                for(var j = 0; j < this.GLOBAL.schedCore.config.serverCluster; j++) {
                    if(j >= obj.list.length) {
                        this.utilizationsList[j].data.push('-')
                    }else {
                        this.utilizationsList[j].data.push({
                            name: obj.list[j].name,
                            value: obj.list[j].number
                        })
                    }
                }
            }
            // console.log(this.utilizationsList)
        },
        changeGlobalData: function(val) {

        },
        generateGraph: function() {
            var myChart = echarts.init(document.getElementById('main'))

            // var date = [];
            //
            // var data = [];

            var that = this
            myChart.setOption({
                "animation": true,
                "title": {
                    "text": "Utilizations",
                    "left": "center",
                    "textStyle": {
                        color: '#c4c6cd'
                    }
                    // "textVerticalAlign": "center"
                },
                "tooltip": {
                    "trigger": "axis",
                    "axisPointer": {
                        "type": "shadow"
                    },
                    "triggerOn": "mousemove|click"
                },
                "textStyle": {
                    color: 'white',
                    fontSize: 14
                },
                "dataZoom": [{
                    "show": true,
                    "type": "slider",
                    "start": 0,
                    "end": 1,
                    "orient": "horizontal"
                }, {
                    "show": true,
                    "type": "inside",
                    "start": 50,
                    "end": 95,
                    "orient": "horizontal"
                }],
                "grid": {
                    "left": "3%",
                    "right": "10%",
                    "bottom": "7%",
                    "containLabel": true
                },
                "yAxis": {
                    "type": "value",
                    "label": "Number",
                    "name": "Count",
                    "nameLocation": "center",
                    "nameGap": 40
                },
                "xAxis": {
                    "type": "category",
                    "name": "Time",
                    "nameLocation": "center",
                    "nameGap": 40,
                    "data": that.time,
                    "axisTick": {
                        "alignWithLabel": false
                    },
                    "boundaryGap": true,
                    "splitLine": {
                        "show": false
                    }
                },
                "series": this.utilizationsList
            })

            myChart.on('dblclick', function (params) {
                var index = params.dataIndex
                that.popupDetail(index)

            })


        },

        popupDetail: function(index) {
            // console.log(that.GLOBAL.schedCore.running.serverLog[index])
            // console.log(index)
            var id = 'area-chart-id-' + index.toString()
            if(!this.GLOBAL.areaJobsDetailsList.includes(id)) {
                var referenceNode = document.getElementById("add-area-chart")
                var newDiv = document.createElement('div')
                newDiv.setAttribute("id", id)
                referenceNode.appendChild(newDiv)
                var ComponentClass = Vue.extend(Jobs)
                var instance = new ComponentClass({
                    propsData: { idIndex: index }
                })
                instance.$mount()
                newDiv.appendChild(instance.$el)
                this.GLOBAL.areaJobsDetailsList.push(id)
                this.popupAFrame(id)
            }

            // this.popupAFrame(id)
            // var referenceNode = document.getElementsByTagName("html")[0]
            // referenceNode.appendChild(instance.$el)

            // layer use end
        },
        deleteEleById: function(id) {
            var ele = document.getElementById(id)
            if(ele != null && 'parentNode' in ele) ele.parentNode.removeChild(ele)
            // var fakeEle = document.getElementById("add-area-chart")
            // if(fakeEle != null && 'parentNode' in fakeEle) {
            //     var referenceNode = document.getElementsByTagName("html")[0]
            //     fakeEle.parentNode.removeChild(fakeEle)
            //     referenceNode.appendChild(fakeEle)
            // }
        },
        popupAFrame: function(id) {
            var thatVue = this
            layui.use('layer', function(){
                var $ = layui.jquery, layer = layui.layer
                var that = this;
                layer.open({
                      type: 1,
                      title: 'Jobs details',
                      area: ['600px', '300px'],
                      shade: 0,
                      maxmin: true,
                      offset: [
                        200,
                        $(window).width() - $(window).width() / 1.5 + (Math.random() * 100),
                      ],
                      content: $('#' + id),
                      btn: ['Close All'],
                      yes: function(index, layero){
                        layer.closeAll();
                        for(var i = 0; i < thatVue.GLOBAL.areaJobsDetailsList.length; i++) {
                            thatVue.deleteEleById(thatVue.GLOBAL.areaJobsDetailsList[i])
                        }
                        var layuiLayerPages = document.getElementsByClassName('layui-layer-page')
                        if(layuiLayerPages != null) {
                            for(var j = 0; j < layuiLayerPages.length; j++) {
                                layuiLayerPages[j].style.top = '-1200px'
                            }
                        }
                        thatVue.GLOBAL.areaJobsDetailsList = []
                      },
                      cancel: function(index, layero) {
                          thatVue.deleteEleById(id)
                          layero[0].style.top = '-1200px'
                          var newArr = []
                          for(var i in thatVue.GLOBAL.areaJobsDetailsList) {
                              if(i != id) newArr.push(i)
                          }
                          thatVue.GLOBAL.areaJobsDetailsList = newArr
                      },
                      zIndex: layer.zIndex,
                      success: function(layero){
                        layer.setTop(layero);
                      }
                 })
                 // open end

            })

        },

    },
    components: {
        // apexchart: VueApexCharts,
        Jobs
    }
}
</script>

<style lang="css" scoped>
.utilizations {
    width: 100%;
    height: 100%;
    overflow-y: scroll;
}
#main {
    width: 100%;
    height: 60%;
    /* margin-top: 20px; */
}
#jobs-details {
    width: 100%;
    height: 100%;
