var Objects = {

    JobGenerator: function(jobsConfig) {
        this.id = jobsConfig.id
        this.sizeFun = jobsConfig.sizeFun
        this.sizeFunPara = jobsConfig.sizeFunPara
        this.speedupFun = jobsConfig.speedupFun
        this.speedupFunPara = jobsConfig.speedupFunPara
        this.color = jobsConfig.color
        this.totalNumber = 0
        this.totalSize = 0
        this.realRate = jobsConfig.realRate
        this.mean = getMean(jobsConfig.sizeFun, jobsConfig.sizeFunPara)
        this.nextGenerateTime = schedCore.globalTime + getNextExponentialValue(jobsConfig.realRate)
        // {
        //     time: 0,
        //     size: 0,
        //     id: 0,
        // }
        this.log = []

    },

    Job: function (id, sizeFun, sizeFunPara, speedupFun, speedupFunPara, jobClass, generateTime) {
        this.id = id
        this.size = getJobSize(sizeFun, sizeFunPara)
        this.currentSize = this.size
        this.jobClass = jobClass
        this.speedupFun = speedupFun
        this.speedupFunPara = speedupFunPara
        this.name = jobClass + '_' + id
        this.usedNodeNumber = 0
        this.speedup = speedUpFunc(speedupFun, speedupFunPara, this.usedNodeNumber)
        this.leftTime = Infinity
        this.generateTime = generateTime
        this.completedTime = 0

        // {
        //     time: 0,
        //     size: 10,
        // }
        this.log = []
    }
}

function getMean(sizeFun, sizeFunPara) {
    var arr = sizeFunPara.split(',')
    for(var i = 0; i < arr.length; i++) {
        arr[i] = parseFloat(arr[i])
    }
    var res = 0
    if(sizeFun == 'exponential') {
        res = 1 / arr[0]
    }else if(sizeFun == 'deterministic') {
        res = arr[0]
    }else if(sizeFun == 'uniform') {
        res = (arr[1] - arr[0]) / 2
    }else if(sizeFun == 'erlang') {
        res = arr[0] / arr[1]
    }else if(sizeFun == 'pareto') {
        res = arr[2] + arr[1] / (arr[0] - 1)
    }
    return res
}

// global


function shedulingAlgorithm(schedCore, gloablFunctions) {

    var currentTime = schedCore.globalTime
    var currentAvailableNodeNum = schedCore.running.availableNodeNum

    // sort the server task list based on the remaining time
    schedCore.running.serverTaskList.sort(function(a, b){
        return a.leftTime - b.leftTime
    })
    var doneJobs = []
    var allowRunTime = 0

    // get completed jobs
    if(schedCore.running.serverTaskList.length != 0) {
        doneJobs.push(schedCore.running.serverTaskList[0]);
        for(var i = 1; i < schedCore.running.serverTaskList.length; i++) {
            if(schedCore.running.serverTaskList[0].leftTime == schedCore.running.serverTaskList[i].leftTime) {
                doneJobs.push(schedCore.running.serverTaskList[i])
            }else {
                break;
            }
        }
    }

    if(doneJobs.length != 0) {
        allowRunTime = doneJobs[0].leftTime
    }

    for(var j = 0; j < doneJobs.length; j++) {
        currentAvailableNodeNum += doneJobs[j].usedNodeNumber
        var currJob = doneJobs[0]
        currJob.currentSize = 0
        currJob.leftTime = 0
        currJob.usedNodeNumber = 0
        currJob.speedup = 0
        currJob.completedTime = currentTime + allowRunTime
        currJob.log.push({
            time: currentTime + allowRunTime,
            size: 0
        })
        schedCore.running.totalCostTimeForCompletedJobs += currJob.completedTime - currJob.generateTime
        schedCore.running.completedQueue.push(currJob)
        schedCore.running.serverTaskList.shift()
        schedCore.running.currentMeanTime = gloablFunctions.getMeanTime(schedCore)

        schedCore.running.meanTimeLog.push(
            {
                totalTime: schedCore.running.totalCostTimeForCompletedJobs,
                num: schedCore.running.completedQueue.length,
                meanTime: schedCore.running.currentMeanTime
            }
        )

    }
    // handle remaining jobs in the task list
    for(var k = 0; k < schedCore.running.serverTaskList.length; k++) {
        var currJob_1 = schedCore.running.serverTaskList[k]
        currJob_1.currentSize -= currJob_1.speedup * allowRunTime
        currJob_1.leftTime = currJob_1.currentSize / currJob_1.speedup
    }
    var newJobs = []
    var newJobsObj = gloablFunctions.getNewJobs(schedCore, allowRunTime)

    newJobs = newJobsObj.newJobs
    schedCore.running.queue = schedCore.running.queue.concat(newJobs)

    // add new jobs to queue

    while(currentAvailableNodeNum > 0){
        if(schedCore.running.queue.length > 0) {
            schedCore.running.serverTaskList.push(schedCore.running.queue[0])
            schedCore.running.queue.shift()
            currentAvailableNodeNum -= 1
        }else {
            break
        }
    }

    currentTime += allowRunTime


    if(currentTime < newJobsObj.currentTime) {
        currentTime = newJobsObj.currentTime
    }

    // reassign variable for jobs
    if(schedCore.running.serverTaskList.length != 0) {
        currentAvailableNodeNum = 0
        var mod = schedCore.config.serverCluster % schedCore.running.serverTaskList.length
        var nodeNum = Math.floor(schedCore.config.serverCluster / schedCore.running.serverTaskList.length)

        for(var l = 0; l < schedCore.running.serverTaskList.length; l++) {
            var currJob_2 = schedCore.running.serverTaskList[l]
            currJob_2.usedNodeNumber = nodeNum
            currJob_2.speedup = gloablFunctions.speedUpFunc(currJob_2.speedupFun, currJob_2.speedupFunPara, nodeNum)
            currJob_2.leftTime = currJob_2.currentSize / currJob_2.speedup
            currJob_2.log.push({
                time: currentTime,
                size: currJob_2.currentSize,
            })
        }
        if(mod != 0) {
            schedCore.running.serverTaskList[0].usedNodeNumber += mod
            schedCore.running.serverTaskList[0].speedup = gloablFunctions.speedUpFunc(schedCore.running.serverTaskList[0].speedupFun,
                schedCore.running.serverTaskList[0].speedupFunPara,
                schedCore.running.serverTaskList[0].usedNodeNumber)
            schedCore.running.serverTaskList[0].leftTime = schedCore.running.serverTaskList[0].currentSize / schedCore.running.serverTaskList[0].speedup
        }
    }

    var slog = {
        time: currentTime,
        usedNumber: schedCore.config.serverCluster - currentAvailableNodeNum,
        list: []
    }
    for(var q = 0; q < schedCore.running.serverTaskList.length; q++) {
        slog.list.push({
            name: schedCore.running.serverTaskList[q].name,
            number: schedCore.running.serverTaskList[q].usedNodeNumber
        })
    }
    schedCore.running.serverLog.push(slog)
    schedCore.globalTime = currentTime
    schedCore.running.availableNodeNum = currentAvailableNodeNum

    return schedCore

}

// local


function getNextExponentialValue(para) {
    var rateParameter = 1.0;
    if(para != 1 && para != 0) rateParameter = para;
    return -Math.log(1.0 - Math.random()) / rateParameter;
}

function getJobSize(sizeFun, sizeFunPara) {
    var arr = sizeFunPara.split(',')
    for(var i = 0; i < arr.length; i++) {
        arr[i] = parseFloat(arr[i])
    }
    var res = 0
    while(res < 0.000001) {
        if(sizeFun == 'exponential') {
            res = exponential(arr)
        }else if(sizeFun == 'deterministic') {
            res = deterministic(arr)
        }else if(sizeFun == 'uniform') {
            res = uniform(arr)
        }else if(sizeFun == 'erlang') {
            res = erlang(arr)
        }else if(sizeFun == 'pareto') {
            res = pareto(arr)
        }
    }
    return res

}

function speedUpFunc(name, para, k) {
    para = parseFloat(para)
    if(k == 0) return 0
    if(name == 'power') {
        return Math.pow(k, para) * 1.0
    }else if(name == 'amdahl') {
        return 1/(1 - para + para/k) * 1.0
    }else  {
        return para * 1.0
    }
}

function getNewJobs(schedCore, allowRunTime) {
    // return
    // {
    //     currentTime: 0,
    //     newJobs: []
    // }
    schedCore.running.jobGeneratorQueue.sort(function(a, b) {
        return a.nextGenerateTime - b.nextGenerateTime
    })

    var res = {
        currentTime: 0,
        newJobs: []
    }

    if(allowRunTime == 0) {
        res.currentTime = schedCore.running.jobGeneratorQueue[0].nextGenerateTime
        var job1 = new Objects.Job(
            // id
            schedCore.running.jobGeneratorQueue[0].totalNumber.toString(),
            // sizeFun
            schedCore.running.jobGeneratorQueue[0].sizeFun,
            schedCore.running.jobGeneratorQueue[0].sizeFunPara,
            schedCore.running.jobGeneratorQueue[0].speedupFun,
            schedCore.running.jobGeneratorQueue[0].speedupFunPara,
            schedCore.running.jobGeneratorQueue[0].id,
            res.currentTime
        )
        res.newJobs.push(job1)
        schedCore.running.jobsDict[job1.name] = job1
        schedCore.running.jobGeneratorQueue[0].log.push({
            time: job1.generateTime,
            size: job1.size,
            id: job1.id
        })
        schedCore.running.jobGeneratorQueue[0].totalNumber += 1
        schedCore.running.jobGeneratorQueue[0].totalSize += job1.size
        schedCore.running.jobGeneratorQueue[0].nextGenerateTime = res.currentTime + getNextExponentialValue(schedCore.running.jobGeneratorQueue[0].realRate)
    }else {
        var allowAllTime = schedCore.globalTime + allowRunTime

        while(allowAllTime >= schedCore.running.jobGeneratorQueue[0].nextGenerateTime) {
            var job2 = new Objects.Job(
                schedCore.running.jobGeneratorQueue[0].totalNumber.toString(),
                schedCore.running.jobGeneratorQueue[0].sizeFun,
                schedCore.running.jobGeneratorQueue[0].sizeFunPara,
                schedCore.running.jobGeneratorQueue[0].speedupFun,
                schedCore.running.jobGeneratorQueue[0].speedupFunPara,
                schedCore.running.jobGeneratorQueue[0].id,
                schedCore.running.jobGeneratorQueue[0].nextGenerateTime
            )
            res.newJobs.push(job2)
            schedCore.running.jobsDict[job2.name] = job2
            schedCore.running.jobGeneratorQueue[0].log.push({
                time: job2.generateTime,
                size: job2.size,
                id: job2.id
            })
            schedCore.running.jobGeneratorQueue[0].totalNumber += 1
            schedCore.running.jobGeneratorQueue[0].totalSize += job2.size
            schedCore.running.jobGeneratorQueue[0].nextGenerateTime += getNextExponentialValue(schedCore.running.jobGeneratorQueue[0].realRate)

            schedCore.running.jobGeneratorQueue.sort(function(a, b) {
                return a.nextGenerateTime - b.nextGenerateTime
            })

        }
    }
    schedCore.running.totalJobsNum += res.newJobs.length
    // console.log(schedCore)
    // update running.lastEleventJobs
    var lastEleventJobs = []
    var count = 11
    for(var m = res.newJobs.length - 1; m >= 0 && count > 0; m--) {
        lastEleventJobs.push(res.newJobs[m].size.toFixed(4))
        count--
    }
    for(var k = schedCore.running.lastEleventJobs.length - 1; k >= 0 && count > 0; k--) {
        // console.log(schedCore.running.lastEleventJobs[k])
        lastEleventJobs.push(schedCore.running.lastEleventJobs[k])
        count--
    }
    var left = 0
    var right = lastEleventJobs.length - 1
    while(right > left) {
        var tem = lastEleventJobs[left]
        lastEleventJobs[left] = lastEleventJobs[right]
        lastEleventJobs[right] = tem
        right--
        left++
    }
    schedCore.running.lastEleventJobs = lastEleventJobs
    return res
}

function exponential(arr) {
    var rateParameter = arr[0];
    if(arr != 1 && arr.length == 0) rateParameter = (1 / 40);
    return -Math.log(1.0 - Math.random()) / rateParameter;
}

function deterministic(arr) {
    var c = 1;
    if(arr.length != 0) c = arr[0];
    return c;
}

function uniform(arr) {
    var a = 0;
    var b = 2;
    if(arr.length != 0) {
        a = arr[0];
        b = arr[1];
    }
    return a + Math.random() * (b-a);
}

function erlang(arr) {
    var n = 2;
    var lambda = 1;
    if(arr.length != 0) {
        n = arr[0];
        lambda = arr[1];
    }
    var total = 0.0;
    for (var i = 0; i < n; i++)
        total += -Math.log(1-Math.random()) / lambda;
    return total;
}

function pareto(arr) {
    var alpha = alpha | 2;
    var sigma = sigma | 1;
    var mu = mu | sigma;
    if(arr.length != 0) {
        alpha = arr[0];
        sigma = arr[1];
        mu = arr[2];
    }
    return mu + sigma * (Math.pow(1-Math.random(), -1/alpha) - 1);
}

function getMeanTime(schedCore) {
    if(schedCore.running.completedQueue.length != 0) {
        return (schedCore.running.totalCostTimeForCompletedJobs / schedCore.running.completedQueue.length)
    }
    return 0.0
}


export default {
    name: 'globalVariables',
    schedCore,
    Objects,
    shedulingAlgorithm,
    getMeanTime,
    getNewJobs,
    speedUpFunc,
    generatorPieOption,
    areaJobsDetailsList,
    schedCoreBackUp
}
